.popular,
.user__lists {
  padding: 2rem 4rem;
}

.popular {
  margin-top: 4rem;
}

.user__lists {
  margin-top: 2rem;
}

.res__grid {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 4rem;
  margin-top: 4rem;
  list-style: none;
  height: 100%;
}
